.home{
    flex-grow: 1;
    min-width: 100%;
    display: flex;
    min-height: 100vh;
    flex-direction: column;
    align-items: center;                        
    background-color:#282c34; 
}

/* display: flex;
margin: 0 auto;
max-width: 1280 px;
padding: 0 15px;
min-height: 100vh;
border-radius: 10px;
flex-direction: column; */

.home .title{
    width: 100%;
    font-size: 10px;
    text-align: center;
}

.home .title p {
    line-height: 50px;
}

.home .person{
    width: 100%;
    margin-top: 20px;
}

.home .person img{
    width: 88%;

}

@media (min-width: 880px){
    .home{
        flex-direction: row;
        justify-content: space-between;
    }
    .home .title,
    .home .person {
        width: 50%;
        margin-top: 0;
        text-align: left;
        font-size: 25px;
    }
    .home .person img {
        width: 100%;
        
    }
}

